.nav-bar {
	display: flex;
	justify-content: space-between;
	box-shadow: 0px 1px 10px #999;
	position: fixed;
	top: 0;
	width: 100%;
	height: 100px;
	padding: 0 30px;
	background-color: white;
	z-index: 5;
}

//tablet
@media screen and (max-width: 950px) {
	.nav-bar {
		height: 50px;
	}
}
