.service-tile {
  width: 325px;
  height: 350px;
  display: flex;
  flex-direction: column;
  margin: 0 5px;
  margin-bottom: 20px;
  border-radius: 4px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  overflow: hidden;
}

.img-container {
  width: 100%;
  height: 70%;
  overflow: hidden;
}

.service-tile-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 1s ease;
}

.description-container {
  width: 100%;
  height: 30%;
}
.service-description {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  height: 100%;
  font-size: 24px;
  background-color: rgb(183, 223, 241);
}

//effects
.service-tile:hover .service-tile-image {
  transform: scale(1.1);
  // transition: all 1s ease;
}

@media screen and (max-width: 950) {
  .service-tile {
    width: 60%;
  }
}

